import React from 'react';

import EquipmentTreeItem from './EquipmentTreeItem';
import EquipmentTreeImage from '../../images/Equipment_tree.png';
import styles from './EquipmentTree.module.css';

const EquipmentTree = ({gear,selected,handleSlotSelection}) => {
  
        const {head,cape,neck,ammo,weapon,body,shield,legs,hands,feet,ring} = gear; 
        return (
            <div className={styles.content}>
                <img src={EquipmentTreeImage} alt="poop" className={styles.equipmentTree}/>
                <EquipmentTreeItem slot="head" itemId={head} selected={selected} handleSlotSelection={handleSlotSelection} />
                <EquipmentTreeItem slot="cape" itemId={cape} selected={selected} handleSlotSelection={handleSlotSelection}/>
                <EquipmentTreeItem slot="neck" itemId={neck} selected={selected} handleSlotSelection={handleSlotSelection}/>
                <EquipmentTreeItem slot="ammo" itemId={ammo} selected={selected} handleSlotSelection={handleSlotSelection}/>
                <EquipmentTreeItem slot="weapon" itemId={weapon} selected={selected} handleSlotSelection={handleSlotSelection}/>
                <EquipmentTreeItem slot="body" itemId={body} selected={selected} handleSlotSelection={handleSlotSelection}/>
                <EquipmentTreeItem slot="shield" itemId={shield} selected={selected} handleSlotSelection={handleSlotSelection}/>
                <EquipmentTreeItem slot="legs" itemId={legs} selected={selected} handleSlotSelection={handleSlotSelection}/>
                <EquipmentTreeItem slot="hands" itemId={hands} selected={selected} handleSlotSelection={handleSlotSelection}/>
                <EquipmentTreeItem slot="feet" itemId={feet} selected={selected} handleSlotSelection={handleSlotSelection}/>
                <EquipmentTreeItem slot="ring" itemId={ring} selected={selected} handleSlotSelection={handleSlotSelection}/>
                
            </div>
        );
}

export default EquipmentTree;