import React from 'react';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';


import { fetchItemsForSlot } from './api';
import { EquipmentTree, EquipmentPicker } from './components';
import styles from './App.module.css';


class App extends React.Component {
    state = {
        gear: {
            head:null,
            cape:null,
            neck:null,
            ammo:null,
            weapon:null,
            body:null,
            shield:null,
            legs:null, 
            hands:null,
            feet:null,
            ring:null},
        selected:null,
    }

    async componentDidMount() {
        //
    }

    handleSlotSelection = async(slot) => {
        console.log("Picked: "+slot);
        const fetchedData = await fetchItemsForSlot(slot);
        console.log(fetchedData);
        this.setState({selected:slot,data:fetchedData});
    }

    handleEquipItem = (itemId) => {
        console.log("trying to equip item ",itemId,"to ",this.state.selected)
        switch (this.state.selected){
            case "head":
                this.setState({gear: { ...this.state.gear , head:itemId}});
                console.log(this.state);
                break;
            case "cape":
                this.setState({gear: { ...this.state.gear , cape:itemId}});
                break;
            case "neck":
                this.setState({gear: { ...this.state.gear , neck:itemId}});
                break;
            case "ammo":
                this.setState({gear: { ...this.state.gear , ammo:itemId}});
                break;
            case "weapon":
                this.setState({gear: { ...this.state.gear , weapon:itemId}});
                break;
            case "body":
                this.setState({gear: { ...this.state.gear , body:itemId}});
                break;
            case "shield":
                this.setState({gear: { ...this.state.gear , shield:itemId}});
                break;
            case "legs":
                this.setState({gear: { ...this.state.gear , legs:itemId}});
                break;
            case "hands":
                this.setState({gear: { ...this.state.gear , hands:itemId}});
                break;
            case "feet":
                this.setState({gear: { ...this.state.gear , feet:itemId}});
                break;
            case "ring":
                this.setState({gear: { ...this.state.gear , ring:itemId}});
                break;
            default:
                break;
        }
    }
    
    render() {
        const { gear,selected,data,open } = this.state;
        return (
            <div>
                <Drawer
                    className={styles.drawer}
                    variant="permanent"
                    anchor="left"
                    open={open}
                >
                    <Divider />
                    
                    <List>
                        <ListItem button key={"text"}>
                            <ListItemText primary={"text"} />
                        </ListItem>
                    </List> 
                    <Divider />
                </Drawer>
                <main className={styles.content}>
                    
                    <EquipmentTree gear={gear} selected={selected} handleSlotSelection={this.handleSlotSelection}/>
                    
                    <EquipmentPicker data={data} handleEquipItem={this.handleEquipItem}/>

                    
                </main>
            </div>
        )
    }
}

export default App